/* TutorDashboard.css - Simplificado */

.tutor-dashboard-container {
  min-height: 100vh;
  background: var(--bg-secondary);
  color: var(--text-primary);
  font-family: var(--font-family);
}

/* Específicos del tutor dashboard */
.students-section h1,
.requests-section h1,
.sessions-section h1,
.create-session-section h1 {
  font-size: var(--font-size-2xl);
  margin-bottom: var(--spacing-xl);
  text-align: center;
}

.students-grid,
.requests-grid,
.sessions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: var(--spacing-lg);
}

.student-card,
.request-card,
.session-card {
  background: var(--bg-primary);
  border-radius: var(--radius-md);
  padding: var(--spacing-lg);
  transition: var(--transition-normal);
  box-shadow: var(--shadow-sm);
}

.student-card:hover,
.request-card:hover,
.session-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.student-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-md);
}

.student-avatar {
  font-size: var(--font-size-3xl);
  color: white;
  background: var(--primary-gradient);
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-full);
}

.student-info h3 {
  margin: 0;
  font-size: var(--font-size-lg);
  font-weight: 600;
  color: var(--text-primary);
}

.student-email {
  margin: var(--spacing-xs) 0;
  color: var(--text-muted);
  font-size: var(--font-size-sm);
}

.student-details,
.request-content {
  margin-bottom: var(--spacing-md);
}

.student-details p,
.request-content p {
  margin: var(--spacing-sm) 0;
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
}

.student-actions,
.request-actions,
.session-actions {
  display: flex;
  gap: var(--spacing-sm);
  flex-wrap: wrap;
}

.request-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: var(--spacing-md);
}

.request-header h3 {
  margin: 0;
  font-size: var(--font-size-lg);
}

.request-meta {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
  align-items: flex-end;
}

.session-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: var(--spacing-md);
}

.session-header h3 {
  margin: 0;
  font-size: var(--font-size-xl);
  font-weight: 600;
}

.session-description {
  margin-bottom: var(--spacing-md);
  opacity: 0.9;
  font-style: italic;
}

.session-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-md);
}

.session-info p,
.session-stats p {
  margin: var(--spacing-sm) 0;
  font-size: var(--font-size-sm);
}

/* Create Session Form */
.session-form-container {
  max-width: 800px;
  margin: 0 auto;
}

.session-form {
  background: var(--bg-primary);
  border-radius: var(--radius-md);
  padding: var(--spacing-xl);
  box-shadow: var(--shadow-sm);
}

/* Responsive Design */
@media (max-width: 768px) {

  .students-grid,
  .requests-grid,
  .sessions-grid {
    grid-template-columns: 1fr;
  }

  .session-details {
    grid-template-columns: 1fr;
  }

  .student-actions,
  .request-actions,
  .session-actions {
    flex-direction: column;
  }
}

@media (max-width: 480px) {

  .students-section h1,
  .requests-section h1,
  .sessions-section h1,
  .create-session-section h1 {
    font-size: var(--font-size-xl);
  }

  .stat-card {
    flex-direction: column;
    text-align: center;
  }

  .activity-item {
    flex-direction: column;
    text-align: center;
  }

  .request-header,
  .session-header {
    flex-direction: column;
    gap: var(--spacing-md);
  }

  .request-meta {
    align-items: flex-start;
  }

  /*  Clases (TutorClassesPage)  */

  .group-card {
    border: 1px solid #E5E7EB;
    border-radius: 12px;
    background: #fff;
    box-shadow: var(--shadow-sm);
    padding: 16px;
  }

  .group-header {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 16px;
    padding-bottom: 12px;
    border-bottom: 2px solid #F3F4F6;
    text-align: center;
  }

  .group-avatar {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    color: #ffffff;
    /* Ensure strong contrast: darker gradient + fallback background color */
    background-color: #0f0b16;
    background-image: linear-gradient(135deg, #2B0A4A 0%, #071020 100%);
    /* subtle shadow to boost perceived contrast on varied backgrounds */
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
    object-fit: cover;
  }

  .group-title {
    margin: 0;
    font-size: 18px;
    font-weight: 700;
  }

  .group-sub {
    margin: 0;
    font-size: 14px;
    color: #6B7280;
  }

  .reservation-row {
    display: grid;
    grid-template-columns: 1fr auto auto;
    gap: 16px;
    align-items: center;
    padding: 12px;
    background: #F9FAFB;
    border: 1px solid #E5E7EB;
    border-radius: 8px;
  }

  .status-badge {
    line-height: 1;
    padding: 6px 12px;
    border-radius: 999px;
    font-size: 12px;
    font-weight: 700;
  }

  .reservation-actions {
    display: flex;
    gap: 8px;
  }

  /* Make all action buttons (Aceptar / Cancelar / Contactar) the same size */
  .reservation-actions .btn {
    min-width: 120px; /* mismo ancho */
    height: 36px;     /* misma altura */
    padding: 0 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    border-radius: 8px;
  }

  .reservation-actions .btn-primary {
    /* Darker indigo to ensure sufficient contrast with white text */
    background: #4F46E5;
    color: #ffffff;
    border: 1px solid #4F46E5;
  }

  .reservation-actions .btn-danger {
    background: #EF4444;
    color: #000;
    border: 1px solid #EF4444;
  }

  .reservation-actions .btn:hover {
    filter: brightness(1.05);
  }
  .dashboard-header {
  position: sticky;
  top: 0;
  z-index: 50;
}

.dashboard-header .header-content {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* empuja acciones a la derecha siempre */
.dashboard-header .header-actions {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 12px;
  min-width: 260px; /* evita solapamiento en resoluciones estrechas */
}

/* contenedor del switch + add role */
.tutor-top-nav {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* botones compactos, misma altura que el avatar */
.tutor-top-nav button,
.header-actions .btn,
.user-menu-container .user-avatar {
  height: 36px;
  padding: 6px 12px;
  border-radius: 10px;
  line-height: 1;
}

/* dropdown del usuario encima de todo */
.user-menu-container {
  position: relative;
  z-index: 60;
}

@media (max-width: 1100px) {
  .main-nav { flex-wrap: wrap; row-gap: 8px; }
  .dashboard-header .header-actions { gap: 8px; min-width: unset; }
}
}

/* Additional reservation styles (desktop & general) */
.reservation-row {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 16px;
  align-items: center;
  padding: 12px;
  background: #F9FAFB;
  border: 1px solid #E5E7EB;
  border-radius: 8px;
}

.reservation-info { display: flex; flex-direction: column; }
.reservation-datetime { margin: 0; font-weight: 600; font-size: 14px; }
.reservation-id { margin: 4px 0 0 0; font-size: 12px; color: #6B7280; }
.reservation-attended { margin-left: 8px; }

.reservation-meta { display: flex; justify-content: center; }
.reservation-actions { display: flex; gap: 8px; justify-content: flex-end; }

.status-badge { color: inherit; font-weight: 700; font-size: 12px; padding: 4px 12px; border-radius: 999px; }

@media (max-width: 768px) {
  .reservation-row { grid-template-columns: 1fr; }
  .reservation-actions { justify-content: flex-start; }
}
